.image {
  margin: 114px 63px 40px 63px;
}

.image__big {
  height: 427px;
  overflow: hidden;
}

.image__big .image__item {
  height: 427px;
}

.image__big .image__item_video a {
  display: block;
}

.image__big .image__item_video a:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: url(../img/svg/video.svg) center no-repeat;
  z-index: 1;
}

.image__preview {
  overflow: hidden;
  margin-top: 10px;
}

.image__preview .arrows-slider {
  left: 13px;
  right: 13px;
}

.image__preview .image__item {
  cursor: pointer;
  border: 1px solid transparent;
  width: 87px;
  height: 60px;
  margin-right: 5px;
  overflow: hidden;
}

.image__preview .image__item_video:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: url(../img/svg/video.svg) center no-repeat;
  z-index: 1;
}

.image__preview-inner {
  width: 548px;
  margin: auto;
  height: 100px;
  display: flex;
  flex-wrap: wrap;
  flex: 0 0 auto;
}

.gallery-thumbs {
  height: 60px;
  display: flex;
  flex-flow: row nowrap;
  margin: 0 auto;
}

.gallery-thumbs .swiper-pagination {
  display: none;
}

.gallery-top img {
  margin: 30px auto 0;
}

.catalog-item__thumbs {
  grid-area: thumbs;
  display: flex;
  background: #fff;
  position: relative;
}

.catalog-item__thumbs:before {
  content: '';
  height: 3px;
  position: absolute;
  top: -1px;
  width: 100%;
  z-index: 5;
  background: #fff;
  left: 0;
}

@media only screen and (min-width: 768px) {
  .gallery-thumbs {
    align-self: center;
    margin-right: 45px;
    /*.swiper-slide {
			height: 60px;
			max-width: 60px;
		}*/
  }

  .catalog-item__thumbs {
    padding: 0 0 0 50px;
  }
}

@media only screen and (max-width: 1199px) {
  .image__big {
    height: 313px;
  }

  .image__big .image__item {
    height: 313px;
  }

  .image__preview-inner {
    width: 362px;
  }
}

@media only screen and (max-width: 991px) {
  .image__big {
    height: 427px;
  }

  .image__big .image__item {
    height: 427px;
  }

  .image__preview-inner {
    width: 548px;
  }
}

@media only screen and (max-width: 767px) {
  .image__big {
    height: 410px;
  }

  .image__big .image__item {
    height: 410px;
  }

  .image__preview .image__item {
    width: 82px;
    height: 56px;
  }

  .image__preview-inner {
    width: 518px;
  }

  .gallery-thumbs .swiper-pagination {
    display: block;
    position: relative;
    bottom: 0 !important;
    align-self: center;
  }
}

@media only screen and (max-width: 639px) {
  .image__big {
    height: 304px;
  }

  .image__big .image__item {
    height: 304px;
  }

  .image__preview {
    display: none;
  }
}

@media only screen and (max-width: 479px) {
  .image__big {
    height: 63vw;
  }

  .image__big .image__item {
    height: 63vw;
  }
}